<template>
    <div class="modal-layout">
        <div class="darken" @click="closeModal">
            <div class="new-modal change-coins-coin-list-item-modal" @click.stop="">
                <!-- <div class="new-modal-back-button-container">
                    <button class="new-modal-back-button" @click="closeModal"></button>
                </div> -->
                <div class = "modal-content">
                    <div class="rate-wrapper" v-if="selectedPair?.firstCoin && selectedPair?.lastCoin">
                        <CryptoRate :coinsPair="selectedPair" />
                    </div>
                    <p class="select-coins-pair" v-else>
                        {{$localizeFilter('select_coins_pair')}}
                    </p>
                    <div class="pairs-wrapper">
                        <div class="coins-list-left">
                            <div v-for="(coin, idx) in firstCoins" v-bind:key="idx" class="coin-list-item" @click="handleFirstCoinClick(coin.ticker)" :class="isFirstCoinAvailable(coin.ticker) ? 'active' : 'inactive'">
                                <img class="coin-list-item-image" :src="require('@/assets/newImages/coins/icons/' + coin.ticker.toLowerCase() + '_icon.png')">
                                <span class="coin-list-item-text" :class="(coin.ticker === selectedPair?.firstCoin) ? 'selected' : (isFirstCoinAvailable(coin.ticker) ? 'active' : 'inactive')">{{coin.ticker}}</span>
                            </div>
                        </div>
                        <div class="line"></div>
                        <div class="coins-list-right">
                            <div v-for="(coin, idx) in lastCoins" v-bind:key="idx" class="coin-list-item" @click="handleLastCoinClick(coin.ticker)" :class="isLastCoinAvailable(coin.ticker) ? 'active' : 'inactive'">
                                <img class="coin-list-item-image" :src="require('@/assets/newImages/coins/icons/' + coin.ticker.toLowerCase() + '_icon.png')">
                                <span class="coin-list-item-text" :class="(coin.ticker === selectedPair?.lastCoin) ? 'selected' : (isLastCoinAvailable(coin.ticker) ? 'active' : 'inactive')">{{coin.ticker}}</span>
                            </div>
                        </div>

                    </div>
                    <div class="button-wrapper">
                        <button class="basic-button" :disabled="!selectedPair" @click="save">{{$localizeFilter('save')}}</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import CryptoRate from '../exchange/CryptoRate.vue'
import localizeFilter from '@/filters/localize.filter'

export default defineComponent({
    props: {
        initPair: {
            type: Object,
            default: null
        }
    },
    setup() {
    },
    data: ()=> ({
        selectedPair: null
    }),
    components: { CryptoRate },
    computed: {
        coins(){
            return this.$store.getters.exchangeCoins
        },
        coinsPairs() {
            return this.$store.getters.exchangeCoinsPairs
        },
        firstCoins() {
            return this.$store.getters.exchangeFirstCoins
        },
        lastCoins() {
            return this.$store.getters.exchangeLastCoins
        }
    },
    methods: {
        isFirstCoinAvailable(coinTicker) {
            if (this.selectedPair?.lastCoin) {
                const availablePairs = this.coinsPairs.filter(pair => {
                    return pair.lastCoin === this.selectedPair.lastCoin
                })
                const availableCoins = availablePairs.map(pair => {
                    return pair.firstCoin
                })
                return availableCoins.includes(coinTicker)
            } else {
                return true
            }
        },
        isLastCoinAvailable(coinTicker) {
            if (this.selectedPair?.firstCoin) {
                const availablePairs = this.coinsPairs.filter(pair => {
                    return pair.firstCoin === this.selectedPair.firstCoin
                })
                const availableCoins = availablePairs.map(pair => {
                    return pair.lastCoin
                })
                return availableCoins.includes(coinTicker)
            } else {
                return true
            }
        },
        closeModal() {
            this.$emit('closeModal')
        },
        handleFirstCoinClick(ticker) {
            if (ticker === this.selectedPair?.firstCoin) { 
                if (this.selectedPair) {
                    this.selectedPair.firstCoin = null 
                    this.selectedPair.rate = null 
                    this.selectedPair.rateChange = null 
                    console.log(this.firstCoins, this.lastCoins)
                }
                return
            }
            if (ticker === this.selectedPair?.lastCoin) { return }

            this.coinsPairs.forEach(pair => {
                if (pair.firstCoin === ticker) {
                    if (!this.selectedPair) {
                        this.selectedPair = {firstCoin: ticker, lastCoin: null, rate: null, rateChange: null}
                    } else {
                        this.selectedPair.firstCoin = ticker
                        if (pair.lastCoin === this.selectedPair.lastCoin) { this.selectedPair = JSON.parse(JSON.stringify(pair)) }
                    }
                }
            })
        },
        handleLastCoinClick(ticker) {
            if (ticker === this.selectedPair?.lastCoin) { 
                 if (this.selectedPair) {
                    this.selectedPair.lastCoin = null 
                    this.selectedPair.rate = null 
                    this.selectedPair.rateChange = null 
                }
                return
            }
            if (ticker === this.selectedPair?.firstCoin) { return }

             this.coinsPairs.forEach(pair => {
                if (pair.lastCoin === ticker) {
                    if (!this.selectedPair) {
                        this.selectedPair = {firstCoin: null, lastCoin: ticker, rate: null, rateChange: null}
                    } else {
                        this.selectedPair.lastCoin = ticker
                        if (pair.firstCoin === this.selectedPair.firstCoin) { this.selectedPair = JSON.parse(JSON.stringify(pair)) }
                    }
                }
            })
        },
        save() {
            this.$store.commit('SET_SELECTED_COINS_PAIR', this.selectedPair)
            setTimeout(()=>{
                this.$emit('closeModal')
                this.$router.push(`/cabinet/exchange?firstCoin=${this.selectedPair.firstCoin}&lastCoin=${this.selectedPair.lastCoin}`)
            }, 500)
        }
    },
    mounted() {
        this.selectedPair = this.initPair
    }
})
</script>

