<template>
    <div class="exchange">
       <div class="header-block">
            <p class="exchange-header">{{isDemo ? $localizeFilter('exchange_demo') : $localizeFilter('exchange')}}</p>
            <p class="exchange-text">{{$localizeFilter('exchange_text')}}</p>
        </div>
      <img class="statistics-small" src="@/assets/newImages/statistics_small.png" @click="showStatisticsModal=true">
      <div class="content-block" v-if="selectedCoinsPair">
      <div class="main-block">
          <CryptoRate class="crypto-rate" :coinsPair="selectedCoinsPair" @changeCoinsPair="changeCoinsPair"/>

          <BuySellToggle :title="null" :variants="actions" @toggle="toggle" />
          <div class="options-block">
            <ExchangeOptions class="exchange-options" :options="orderTypes" @selectOption='selectOption'/>
            <button class="info-button" @click="showOrderTypeInfoModal=true"></button>
          </div>
          <ExchangeFields :coinsPair="selectedCoinsPair" :type="selectedOrderType" :actionType="selectedAction"/>
          <OrdersList class="orders-list" />
      </div>
     
      <div class="statistics-block">
        <ExchangeStatistics />
      </div>
      </div>
     <ChangeCoinsPairModal v-if="showChangeCoinsPairModal" :initPair="selectedCoinsPair" @closeModal="closeModals"/>
     <StatisticsModal v-if="showStatisticsModal" @closeModal="closeModals"/>
     <OrderTypeInfoModal v-if="showOrderTypeInfoModal" :orderTypes="orderTypes" @closeModal="closeModals"/>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import CryptoRate from '@/components/exchange/CryptoRate.vue'
import ExchangeStatistics from '@/components/exchange/ExchangeStatistics.vue'
import BuySellToggle from "@/components/exchange/BuySellToggle.vue"
import ExchangeOptions from '@/components/exchange/ExchangeOptions.vue'
import OrdersList from '@/components/exchange/OrdersList.vue'
import ChangeCoinsPairModal from '@/components/newModals/ChangeCoinsPairModal.vue'
import StatisticsModal from '@/components/newModals/StatisticsModal.vue'
import OrderTypeInfoModal from '@/components/newModals/OrderTypeInfoModal.vue'
import localizeFilter from '@/filters/localize.filter'
import ExchangeFields from '../components/exchange/ExchangeFields.vue'

export default defineComponent({
    props: {
        firstCoinOverride: {
            type: String,
            default: null
        },
        lastCoinOverride: {
            type: String,
            default: null
        },
        ordersListPageOverride: {
            type: Number,
            default: 0
        },
        isDemoOverride: {
            type: Boolean,
            default: false
        }
    },
    components: {
        CryptoRate,
        ExchangeStatistics,
        BuySellToggle,
        ExchangeOptions,
        OrdersList,
        ChangeCoinsPairModal,
        StatisticsModal,
        OrderTypeInfoModal,
        ExchangeFields
    },
    setup() {
    },
    data: () => ({
      showChangeCoinsPairModal: false,
      showStatisticsModal: false,
      showOrderTypeInfoModal: false
    }),
    computed: {
        selectedCoinsPair() {
            return this.$store.getters.exchangeSelectedCoinsPair
        },
        actions() {
            return this.$store.getters.exchangeActions
        },
        selectedAction() {
            return this.$store.getters.exchangeSelectedAction
        },
        orderTypes() {
            return this.$store.getters.exchangeOrderTypes
        },
        selectedOrderType() {
            return this.$store.getters.exchangeSelectedOrderType
        },
        isDemo() {
            return this.$store.getters.isDemo
        },
    },
    mounted() {
        this.$nextTick(()=> {
            this.$store.commit('SET_DEMO', {isDemo: this.isDemoOverride})
        })

        if (this.firstCoinOverride && this.lastCoinOverride) {
            this.$nextTick(()=> {
                this.$store.commit('SET_SELECTED_COINS_PAIR', {firstCoin: this.firstCoinOverride, lastCoin: this.lastCoinOverride})
            })
        } else {
            if (!this.selectedCoinsPair) {
                setTimeout(()=> {
                    this.showChangeCoinsPairModal = true 
                }, 500)
            }
        }
        this.$nextTick(()=> {
            this.loadData()
        })
    },
    methods: {
        async loadData(){
            await this.$store.dispatch('subscribeExchangeNotifications')
            await this.$store.dispatch('getExchangeOrders', {pair: `${this.selectedCoinsPair.firstCoin}/${this.selectedCoinsPair.lastCoin}`})
        },
        toggle(index) {
            this.$store.commit('SET_SELECTED_EXCHANGE_ACTION', this.actions[index])
        },
        selectOption(idx) {
            this.$store.commit('SET_SELECTED_EXCHANGE_ORDER_TYPE', this.orderTypes[idx])
        },
        changeCoinsPair() {
            this.showChangeCoinsPairModal = true
        },
        closeModals() {
            this.showChangeCoinsPairModal = false
            this.showStatisticsModal = false
            this.showOrderTypeInfoModal = false
        },
    }
})
</script>

