<template>
    <div>
        <div class="place-for-chart">
          <div id="chart">
          <apexchart
            width="335"
            height="300"
            type="candlestick"
            :options="chartOptions"
            :series="series"
          ></apexchart>
          </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import {ref, computed} from 'vue';
import dateTimeFilter from '@/filters/date.filter'
import localizeFilter from '@/filters/localize.filter'
import VueApexCharts from "vue3-apexcharts";
import utils from '@/utils/utils'


export default defineComponent({
  props: {
    animateChart: {
      type: Boolean,
      default: false
    }
  },
  components: {
    apexchart: VueApexCharts,
  },
  watch: {
    animateChart(newVal, oldVal) {
      if (newVal == true && oldVal == false) {
        this.$nextTick(this._animateChart())
      }
    },
  },
  data: ()=> ({
      // showChart: true,
      chartOptions: {
        chart: {
          id: "candlestick-chart",
          toolbar: {
            tools: {
              download: false,
              selection: false,
              zoom: true,
              zoomin: true,
              zoomout: true,
              pan: true,
              reset: false
            }
          }
        },
        yaxis: {
          labels: {
            style: {
              colors: ['#959595']
            },
            offsetX: -15,
            formatter: function (value) {
              if (value && value > 0) {
                return utils.formatCurrency(value, 'USD', 'en-US', 2, 2);
              } else {
                return ''
              }
            }
          },
          axisBorder: {
            color: '#959595',
            height: 1,
            width: '100%',
          },
           axisTicks: { 
            color: '#959595',
            height: 6,
            offsetX: 0,
            offsetY: 0
         }
        },
        xaxis: {
          type: 'datetime',
          labels: {
            style: {
              colors: ['#959595']
            },
            offsetY: 5,
            formatter: function (value) {
              if (value && value > 0) {
                return dateTimeFilter(value, 'custom', 'dd.mm.yy HH:MM');
              } else {
                return ''
              }
            }
          },
          axisBorder: {
            color: '#959595',
            height: 1,
            width: '100%',
          },
          axisTicks: { 
            color: '#959595',
            height: 6,
            offsetX: 0,
            offsetY: 0
          },
          // hide x axis tooltip
          // tooltip: {
          //   enabled: false
          // }
        },
        grid: {
            padding: {
              left: 0,
              bottom: -30
            },
            borderColor: '#959595',
        },
        plotOptions: {
          bar: {
            columnWidth: '70%'
          },
          candlestick: {
            colors: {
              upward: '#399A50',
              downward: '#B54A4A'
            },
            wick: {
              useFillColor: true,
            }
          }
        },
        tooltip: {
          theme: 'dark',
          custom: function({series, seriesIndex, dataPointIndex, w}) {
            // candlestick standard
            const open = w.globals.seriesCandleO[seriesIndex][dataPointIndex]
            const high = w.globals.seriesCandleH[seriesIndex][dataPointIndex]
            const low = w.globals.seriesCandleL[seriesIndex][dataPointIndex]
            const close = w.globals.seriesCandleC[seriesIndex][dataPointIndex]
            return `<div class="candlestick-chart-custom-tooltip">
            <div class="candlestick-chart-custom-tooltip-text"><div class="candlestick-chart-custom-tooltip-text-title">${localizeFilter('candlestick_chart_open')}:</div> <div class="candlestick-chart-custom-tooltip-text-content">${open}</div></div>
            <div class="candlestick-chart-custom-tooltip-text"><div class="candlestick-chart-custom-tooltip-text-title">${localizeFilter('candlestick_chart_high')}:</div> <div class="candlestick-chart-custom-tooltip-text-content">${high}</div></div>
            <div class="candlestick-chart-custom-tooltip-text"><div class="candlestick-chart-custom-tooltip-text-title">${localizeFilter('candlestick_chart_low')}:</div> <div class="candlestick-chart-custom-tooltip-text-content">${low}</div></div>
            <div class="candlestick-chart-custom-tooltip-text"><div class="candlestick-chart-custom-tooltip-text-title">${localizeFilter('candlestick_chart_close')}:</div> <div class="candlestick-chart-custom-tooltip-text-content">${close}</div></div>
            </div>`
          },
        },
      },
  }),
  setup(props) {

  },
  methods: {
    _animateChart() {
      setTimeout(()=> {
        this.showChart = false;
        this.$nextTick(() => {
         this.showChart = true;
        });
      }, 1000)
    },
    async loadData() {
      // try {
      //   await this.$store.dispatch('getCandlestickChartData')
      // }
      // catch(e) {
      //   this.$error(e)
      // }
    },
    onUpdated() {
    },
},
  computed: {
    series() {
      return [
        {
          name: "series-1",
          data: this.$store.getters.candlestickChartData
        }
      ]
    }
  },
  async mounted() {
      this.loadData()
      // this.$store.dispatch('mockCandlestickDataDynamicChanges')
  },
})
</script>

